.iq-ribbon-effect-red {
    background: #C03221;
    display: flex;
    right: 2rem;
    padding: 0.625rem;
    position: absolute;
    text-orientation: mixed;
    text-transform: UPPERCASE;
    top: 0;
    white-space: nowrap;
    -webkit-writing-mode: vertical-rl;
    writing-mode: vertical-rl;
    z-index: 100;
}

.iq-ribbon-effect-red:before {
    border-right: 1.5em solid transparent;
    left: 0;
}

.iq-ribbon-effect-red:after, .iq-ribbon-effect-red:before {
    border-top: 1.5em solid #C03221;
    bottom: -1.5em;
    content: "";
    height: 0;
    position: absolute;
    width: 0;
}

.iq-ribbon-effect-red:after, .iq-ribbon-effect-red:before {
    border-top: 1.5em solid #C03221;
    bottom: -1.5em;
    content: "";
    height: 0;
    position: absolute;
    width: 0;
}
.iq-ribbon-effect-red:after {
    border-left: 1.5em solid transparent;
    right: 0;
}




