.cardbg:hover
{
    box-shadow: 0 20px 45px -15px #000 !important;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
   
} 

.dark .cardbg:hover
{
    box-shadow: 0 15px 35px -15px #fff !important;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
   
} 


.btnw .btn-close {
    filter: invert(1);
}

